<template>
    <h1 class="Title" id="LandingTitle" :style="{ opacity: opacityTitle }">
        {{ title }}
    </h1>
</template>

<script>
import config from "../config"
export default {
    data() {
        return {
            title: "Parallax Scroll Demo",
            timeOut: 3000,
            opacity: 0,
            opacityTitle: 100
        }
    },
    mounted() {
        this.changeTitleWithTransitionAnime()
    },
    methods: {
        changeTitleWithTransitionAnime() {
            const waitPageLoad = new Promise((resv, rejt) => {
                resv(this.timeOut)
            }).then(val => setTimeout(() => val))

            setTimeout(() => {
                this.opacity = 100
            }, this.timeOut - 100)
            setTimeout(() => {
                this.opacityTitle = 0
            }, this.timeOut + 1000)
            setTimeout(() => {
                this.opacityTitle = 100
                this.title = config.USER_INFO.Name.toUpperCase()
            }, this.timeOut + 2000)
        }
    }
}
</script>

<style scoped>
#LandingTitle {
    position: absolute;
    top: 0px;
    z-index: 6;
    color: #fff;
    margin-top: 3.5em;
    font-size: 5em;
    border: #fff;
    border-style: double;
    border-width: 0.2em;
    border-radius: 0.1em;
    width: 9em;
}

.Title {
    transition-property: opacity;
    transition-duration: 1s;
    transition-timing-function: ease-in-out;
}

@media screen and (max-width: 900px) {
    #LandingTitle {
        font-size: var(--rwdTitleSize);
        margin-top: 9em;
    }
}
</style>
